import { ApiService } from '@tableau/api-shared-js';

/**
 * Functionalities related to toolbar
 */
export interface ToolbarService extends ApiService {
  /**
   * Restores the workbook to its original state.
   *
   * @returns Empty promise once the workbook has been reverted
   */
  revertAllAsync(): Promise<void>;

  /**
   * Redoes last undone action on a sheet
   *
   * @returns Empty promise once the action is complete
   */
  redoAsync(): Promise<void>;

  /**
   * Undoes previous action on a sheet
   *
   * @returns Empty promise once the action is complete
   */
  undoAsync(): Promise<void>;

  /**
   * Equivalent to clicking on Download > Image from the toolbar, which creates a PNG file of the current visualization
   *
   * @returns Empty promise once the action is complete
   */
  exportImageAsync(): Promise<void>;
  /**
   * Set the automatic update state
   *
   * @param state boolean the state that automatic update will be set
   */
  setAutoUpdateAsync(state: boolean): Promise<void>;

  /**
   * Displays a dialog that downloads a copy of the original workbook.
   *
   * @returns Empty promise once the action is complete
   */
  displayDownloadWorkbookDialogAsync(): Promise<void>;

  /**
   * Equivalent to clicking on Download > PDF from the toolbar, which shows a dialog allowing the user to select options for downloading a PDF file.
   *
   * @returns Empty promise once the action is complete
   */
  displayExportPdfDialogAsync(): Promise<void>;

  /**
   * Equivalent to clicking on Download > PowerPoint from the toolbar, which shows a dialog allowing the user to choose the sheets from the workbook or dashboard to download in a PowerPoint file.
   *
   * @returns Empty promise once the action is complete
   */
  displayExportPowerpointDialogAsync(): Promise<void>;

  /**
   * Equivalent to clicking on Download > Data from the toolbar. Shows the Export Data dialog, which is currently a popup window.
   *
   * @returns Empty promise once the action is complete
   */
  displayExportDataDialogAsync(): Promise<void>;

  /**
   * Equivalent to clicking on Download > Crosstab from the toolbar. Shows the Export CrossTab dialog. You can choose to download the crosstab data as a CSV file or to cancel the operation.
   *
   * @returns Empty promise once the action is complete
   */
  displayExportCrosstabDialogAsync(): Promise<void>;

  /**
   * Equivalent to clicking on Share from the toolbar, which shows a dialog allowing the user to copy a shareable URL for the viz or copy its embedding code.
   *
   * @returns Empty promise once the action is complete
   */
  displayShareDialogAsync(): Promise<void>;
}
